  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        isAgree: true,
        isSure: false,
        second: 60,
        showText: true,
        phone: '',
        psdErr: false,
        isRequest: false,
		cid:''  //个推id
      }
    },
    onShow() {
			this.cid = plus.push.getClientInfo().clientid,
      uni.showToast({
        title: '你还没有账号哦，赶紧注册一个吧',
        icon: 'none'
      })
			
    },
    methods: {
      checkAgree: function (e) {
        if (e.detail.value.length > 0) {
          this.isAgree = true
        } else {
          this.isAgree = false
        }
      },
      getPhone: function (e) {
        this.phone = e.detail.value
      },
      getIdenCode: function (e) {
        if (e.detail.value.length >= 4) {
          this.showText = true
          this.isSure = true
          this.second = 60
        } else {
          uni.showToast({
            title: '请填写正确的验证码',
            icon: 'none'
          })
        }
      },
      getCode: function () {
        var reg = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
        var phone = this.phone
        if (!reg.test(phone)) {
          uni.showToast({
            title: '请输入正确的手机号',
            icon: 'none'
          })
        } else {
          this.showText = false
          var interval = setInterval(() => {
            --this.second
          }, 1000)
          uni.request({
            url: urls.HTTP + '/user/getmcodenum',
            method: 'POST',
            data: {
              username: phone
            },
			success: (res) => {
				if (res.data.code === 0) {
					uni.showToast({
						title: res.data.error,
						icon: 'none'
					})
				} else {								
					uni.showToast({
						title: res.data.error,
						icon: 'none'
					})
				}
			}
          })
          setTimeout(() => {
            clearInterval(interval)
            this.showText = true
            this.second = 60
          }, 60000)
        }
      },
      submit: function (e) {
        let qqopenid = uni.getStorageSync('qqopenid')
        let unionid = uni.getStorageSync('openid')
        let type = this.$store.state.loginType
        let openid = ''
        if (type === 1) {
          openid = unionid
        } else {
          openid = qqopenid
        }
        if (this.isAgree) {
          if (!e.detail.value.phone || !e.detail.value.code) {
            uni.showToast({
              title: '信息不能为空',
              icon: 'none'
            })
          } else {
            this.isRequest = true
            uni.request({
              url: urls.HTTP + '/user/Binduser',
              method: 'POST',
              data: {
                mobile: e.detail.value.phone,
                code: e.detail.value.code,
                openid: openid,
                type: type,
				cid : this.cid
              },
              success: (res) => {
                // console.log('绑定', res)
                this.isRequest = false
                if (res.data.code === 0) {
                  if (res.data.tip === 1) { // tip值为0表示绑定账号，为1表示添加新用户
                    uni.showModal({
                      title: '提示',
                      content: '自动注册成功，初始密码为123456',
                      showCancel: false,
                      success: () => {
                        uni.setStorageSync('user', res.data.user)
                        uni.setStorage({
                        	key: 'userInfo',
                        	data: JSON.stringify(res.data.user), // 对象转字符串进行本地存储
                        	success: (res) => {
                        		console.log(res)
                        	}
                        })
                        this.$store.dispatch('isLogin', true)
                        if (res.data.user.sex === 0) {
                          uni.redirectTo({
                            url: 'chooseSex'
                          })
                        } else {
                          uni.switchTab({
                            url: '../find/find'
                          })
                        }
                      }
                    })
                  } else {
                    uni.showModal({
                      title: '提示',
                      content: '绑定成功！',
                      showCancel: false,
                      success: () => {
                        uni.setStorageSync('user', res.data.user)
                        uni.setStorage({
                        	key: 'userInfo',
                        	data: JSON.stringify(res.data.user), // 对象转字符串进行本地存储
                        	success: (res) => {
                        		console.log(res)
                        	}
                        })
                        this.$store.dispatch('isLogin', true)
                        uni.switchTab({
                          url: '../find/find'
                        })
                      }
                    })
                  }
                } else {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                }
              }
            })
          }
        } else {
          uni.showToast({
            title: '请先阅读协议并同意',
            icon: 'none'
          })
        }
      }
    }
  }
